body {
  font-family: 'Brawler';
}

.sidebar {
  width: 300px;
  height: 100%;
  position: fixed;
  left: 200px;
  top: 50px;
  font-family: 'Brawler';
}

.sidetext {
  margin-left: 50px;
  margin-top: 180px;
}

.title {
  color: #000;
  display: inline-block;
  overflow: hidden;
  font-size: 18px;
  font-weight: 400;
  position: relative;
  padding: 2px 1px;
  left: 1px;
}

.title::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  transform: translateY(calc(100% - 2px));
  width: 100%;
  height: 100%;
  background: #000;
  transition: transform .1s ease;
}

.title:hover::before {
  transform: translateY(0);
  background: #ffdf00;
  background: var(--main-color);
  transition: transform .1s ease;
}

.angle {
  margin-top: 5px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: bolder;
  color: #ffdf00;
  color: var(--main-color);
  letter-spacing: 0.5px;
  margin-right: 3px;
}

.pfp {
  width: 70px;
  border-radius: 100px;
  margin-bottom: 30px;
}

.links {
  font-size: 15px;
  margin-top: 20px;
}

.links a {
  letter-spacing: 0px;
  margin-bottom: 10px;
  color: black;
  text-decoration: none;
  display: inline-block;
  text-decoration: none;
  line-height: 1;
  position: relative;
  padding: 2px 1px;
  overflow: hidden;
  vertical-align: bottom;
  transition: color .1s ease;
}


.links a::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  transform: translateY(100%);
  width: 100%;
  height: 100%;
  background: #ffdf00;
  background: var(--main-color);
  transition: transform .1s ease-out;
}

.links a:hover { 
  color: #000; 
  text-decoration: none;
}

.links a:hover::before {
  transform: translateY(0);
  transition: transform .1s ease;
}

.icons {
  display: none;
  position: relative;
  left: 2px;
  bottom: 15px;
}

.icons a {
  font-size: 14px;
  color: black;
  margin-right: 15px;
  transition: .15s ease;
}

.icons a:hover {
  color: #ffdf00;
  color: var(--main-color);
  transition: .15s ease;
}

.wrapper {
  padding: 10px;
  margin: 0 auto;
  width: 500px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .item {
    visibility: hidden;
    display: none;
  }

  h2 {
    font-size: 50px;
    margin: 0;
    color: #3a3a3a;
    text-align: left;
  }

  > div {
    min-height: 20px;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: flex;
    align-items: flex-end;
    font-weight: bold;
    color: orange;

    &:after {
content: "";
    width: 10px;
    height: 4px;
    display: block;
    background: black;
    color: orange;
    margin-bottom: 4px;
    margin-left: 2px;
    -webkit-animation-duration: 350ms;
            animation-duration: 350ms;
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    }
  }
}

@-webkit-keyframes fade {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fade {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}


